html,body{
    margin: 0px;
    padding: 0px;
}
.header {
    position: relative;
    width:100%;
    overflow: hidden;
    height: 240px;
    background: #053257 url() repeat 0 0;
    z-index: 2000;
}
.header-title{
    margin: 0px;
    padding: 0px;
    line-height: 240px;
    text-align: center;
}